* {
  margin: 0;
  padding: 0;
}

/* Navbar */
nav {
  /* width: 1512px; */
  width: 100%;
  height: 75px;
  display: flex;
  background: #ffffff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.25);
}

nav .logo {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}

nav .logo strong {
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 23px;
  letter-spacing: 0em;
}

nav .main {
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

nav ul {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  text-decoration: none;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 23px;
  letter-spacing: 0em;
  color: #000000;
}

.active {
  color: #c3ad00;
}

nav .login {
  width: 30%;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginButton {
  height: 43px;
  width: 170px;
  left: 1214px;
  top: 16px;
  border-radius: 6px;
  padding: 10px, 30px, 10px, 30px;
  font-family: Roboto;
  font-style: normal;
  font-size: 20px;
  font-weight: 500;
  line-height: 23px;
  background: #ffffff;
  border: 1.8px solid #000000;
  border-radius: 6px;
}

nav ul li a:hover {
  border-bottom: 2px solid purple;
}

/* Home Background image */
.homeBg {
  /* width: 1521px; */
  width: 100%;
  height: 727px;
  background: url("HomeBg.jpg");
  background-size: cover;
}

.applyBtn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 30px;
  gap: 10px;

  position: absolute;
  width: 170px;
  height: 43px;
  left: 134px;
  top: 592px;

  background: #e8a902;
  border-radius: 6px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  border: 0;
  color: #ffffff;
  cursor: pointer;
}


/* Apply section */
.apply-heading {
  margin: 60px 0;
  font: 400 40px DM Serif Display;
  text-align: center;
}

.card-container {
  margin: auto;
  width: 1260px;
  height: 700px;
  display: grid;
  grid-template-columns: 600px 600px;
  justify-content: space-between;
  align-content: space-between;

}

/* card css */
.card {
  width: 600px;
  height: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content {
  width: 260px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;

}

.card-number {
  font: 400 90px DM Serif Display;
  text-align: left;
  margin: 10px 5px;
}

.card-heading {
  font: 400 30px DM Serif Display;
  text-align: left;
  margin: 10px 5px;
}

.card-para {
  font: 400 22px DM Serif Display;
  text-align: left;
  width: 90%;
  margin: 10px 5px;
  word-wrap: break-word;
}

.card-image {
  width: 300px;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.my-slider {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Testimonial card */
.test-card {
  background-color: #f6f6f6;
  width: 1521px;
  height: 508px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.test-img {
  width: 170px;
  height: 170px;
  border-radius: 50%;
}

.test-para {
  width: 1329px;
  height: 166px;
  word-wrap: break-word;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Alegreya';
  font-style: normal;
  font-weight: 400;
  font-size: 22.3059px;
  line-height: 30px;
  letter-spacing: 0.02em;
}

.test-heading {
  font-family: 'Alegreya';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 0.02em;
}

.swiper-pagination-bullet {
  background-color: #1f1f1f !important;
}

.swiper-pagination-bullet-active {
  background-color: #F2C216 !important;
}

